import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { ArrangementsHttpService } from '@backbase/data-ang/arrangements';

@Injectable()
export class ArrangementsService {
  constructor(private readonly arrangementsHttpService: ArrangementsHttpService) {}

  updateUserPreferences(arrangementId: string, alias: string): Observable<unknown> {
    return this.arrangementsHttpService.updateUserPreferences({ accountUserPreferences: { alias, arrangementId } });
  }
}
